import React from 'react'
import {NavLink} from 'react-router-dom'
import './indexHome.css'

import Navbar from './navbar/navbar'
import Footer from './footer/footer'
import Title from './title/title'
import UserReviews from './reviews/userReviews'
import FeaturedCourses from './featuredCourses/featuredCourses'
import Article from './articlespreview/articlespreview'
import Donate from './donate/donate'
import Contacts from './contato/contato'
import Cadastro from './cadastro/cadastro'
import Courses from './courses/courses'
import Services from './services/services'

export default function index(){
    return(
        <div className='index'>
            <Navbar/>
            <Title titulo='ORDEM DOS CAVALEIROS DE CRISTO' subtitulo='ESCOLA DE MISTRIOS DA ANTIGA RELIGIO'/>
            <Article />
            <FeaturedCourses />
            <UserReviews />
            <Footer/> 
        </div>
    )
}

export function indexContato(){
    return(
        <div className='index'>
            <Navbar/>
            <Contacts/>
            <Footer/>
        </div>
    )
}

export function indexCadastro(){
    
    return(
        <div className='index'>
            <Navbar/>
            <Cadastro/>
            <Footer/>
        </div>
    )
}
export function indexCourses(){
    return(
        <div className='index'>
        <Navbar/>
        <Courses />
        <Footer/>
    </div>
    )
}
export function indexServices (){
    return(
        <div className='index'>
        <Navbar/>
        <Services />
        <Footer/>
    </div>
    )
    
}
export function indexDonate(){
    return(
        <div className='index'>
            <Navbar/>
            <Donate titulo="DOAES" />
            <Footer/>
        </div>
    )
}
export function indexDonateAprovado(){
    return(
        <div className="conteudoAprovacao">
            <Navbar/>
            <div className="conteudoAprovacao">
                <h2>Doao aprovada, muito obrigado pela sua doao!</h2>
                <NavLink to={"/doacoes"}><button className="botaoVoltar">Voltar</button></NavLink>
            </div>
            <Footer/>
        </div>
    )
}
export function indexDonatePendente(){
    return(
        <div className="conteudoAprovacao">
            <Navbar/>
            <div className="conteudoAprovacao">
                <h2>Aguardaremos ansiosamente a sua doao!</h2>
                <NavLink to={"/doacoes"}><button className="botaoVoltar">Voltar</button></NavLink>
            </div>
            <Footer/>
        </div>
    )
}
export function indexDonateReprovado(){
    return(
        <div className="conteudoAprovacao">
            <Navbar/>
            <div className="conteudoAprovacao">
                <h2>Infelizmente sua doao no foi consolidada</h2>
                <h3>Confira suas informaes de pagamento e tente novamente!</h3>
                <NavLink to={"/doacoes"}><button className="botaoVoltar">Voltar</button></NavLink>
            </div>
            <Footer/>
        </div>
    )
}